<template>
  <div class="account-pages mt-5 mb-5">
    <div class="container">
       <div hidden id="spinner"></div>
      <div class="row justify-content-center">
        <div class="col-lg-5">
          <div class="card">
            <!-- Logo -->
            <div class="card-header pt-4 pb-4 text-center bg-primary">
              <a href="index.html">
                <span>
                  <img src="assets/images/logo.png" alt height="18" />
                </span>
              </a>
            </div>

            <div class="card-body p-4">
              <div class="text-center w-75 m-auto">
                <h4 class="text-dark-50 text-center mt-0 font-weight-bold">Giriş Yap</h4>
              </div>

              <form id="loginform">
                <div class="form-group">
                  <label for="emailaddress">E posta adresi</label>
                  <input
                    class="form-control"
                    type="email"
                    v-model="email"
                    required
                    autofocus
                    placeholder="E posta adresinizi giriniz"
                  />
                </div>

                <div class="form-group">
                  <label for="password">Şifre</label>
                  <input
                    class="form-control"
                    type="password"
                    id="password"
                    v-model="password"
                    required
                    placeholder="Şifrenizi giriniz"
                  />
                </div>

                <div class="form-group mb-3">
                  <div class="custom-control custom-checkbox">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="checkbox-signin"
                      checked
                    />
                    <label class="custom-control-label" for="checkbox-signin">Beni Hatırla</label>
                  </div>
                </div>

                <div class="form-group mb-0 text-center">
                  <button class="btn btn-primary" type="submit" @click="handleSubmit">Giriş Yap</button>
                </div>
              </form>
            </div>
            <!-- end card-body -->
          </div>
          <!-- end card -->

          <!-- end row -->
        </div>
        <!-- end col -->
      </div>
      <!-- end row -->
    </div>
    <!-- end container -->
  </div>
  <!-- end page -->
</template>

<script>

import state from '../Helper/custom_state';
import Swal from "sweetalert2";

export default {
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();

      if (this.password.length > 0&&this.email.length>0) {
        document.getElementById("spinner").removeAttribute('hidden');
        this.$http
          .post(`${state.ip}/api/login`, {
            Email: this.email,
            Password: this.password
          })
          .then(response => {
           document.getElementById("spinner").setAttribute('hidden', '');
            if(response.data.success){
          let is_admin = response.data.user.Priority
                        localStorage.setItem('user',JSON.stringify(response.data.user))
                        localStorage.setItem('token',response.data.token)
                        var d = new Date();
                        d.setTime(d.getTime() + (1*1000*60*60));
                        var expires = "expires="+ d.toUTCString();
                        document.cookie =  "accesstoken=" + response.data.token + ";" + expires + ";path=/";
                        
                        if (localStorage.getItem('token') != null){
                            if(this.$route.params.nextUrl != null){
                                this.$router.push(this.$route.params.nextUrl)
                            }
                            else {
                                if(is_admin== 1){
                                    this.$router.push('admin/panel')
                                }
                                else {
                                    this.$router.push('user/panel')
                                }
                            }
                        }
                        }
                        else{
                           Swal.fire("", response.data.msg, "error");
                        }
          })
          .catch(function(error) {
              // eslint-disable-next-line no-console
             console.log(error);
          });
      }
      else{
         Swal.fire("", "Lütfen gerekli bilgileri giriniz", "error");
      }
    }
  }
};
</script>
